.PresentationContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.PresentationForm {
    height: 60%;
    background-color: black;
    padding: 2rem;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


.PresentationForm input,
.PresentationForm textarea,
.PresentationForm select {
    width: 26rem;
    resize: none;
    border-radius: 5px;
    font-size: 0.9rem;
    padding: 1rem;
}

.PresentationForm select {
    width: 28.2rem;
}

.PresentationForm h3 {
    text-align: center;
    color: rgb(62, 196, 62);
}

.PresentationForm a {
    margin-top: 1rem;
    padding: 1rem 2rem;
    background-color: rgb(62, 196, 62);
    box-shadow: 0 0 10px 0 rgb(62, 196, 62);
    color: white;
    border-radius: 2rem;
    text-align: center;
}

.PresentationText {
    font-size: 2rem;
}

.PresentationSchedules {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.PresentationText a {
    text-decoration: none;
    background-color: rgb(62, 196, 62);
    color: white;
    padding: 1rem;
    border-radius: 2rem;
    font-size: 1rem;
}

@media only screen and (max-width:1000px) {
    .PresentationContainer {
        flex-direction: column;
        margin-top: -3rem;
    }

    .PresentationForm input,
    .PresentationForm textarea,
    .PresentationForm select {
        width: 16rem;
        resize: none;
        border-radius: 5px;
        font-size: 0.9rem;
        padding: 1rem;
    }

    .PresentationForm select {
        width: 18.2rem;
    }

    .PresentationForm {
        padding-top: 3rem;
        padding-bottom: 3rem;
    }

    .PresentationText {
        padding-top: 6rem;
        padding-bottom: 3rem;
        font-size: 1rem;
    }
}