.FooterContainer {
    background-color: rgb(15, 15, 15);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.FooterLinks {
    padding-right: 1rem;
    padding-left: .5rem;
}

.FooterContainer img {
    height: 10rem;
}

.FooterContainer a>img {
    height: 3rem;
    opacity: .5;
    transition: .5s ease-in-out;
}

.FooterContainer a>img:hover {
    opacity: 1;
}

.FooterRoute a {
    background-color: rgb(62, 196, 62);
    text-decoration: none;
    padding: 1rem 2rem;
    border-radius: 2rem;
    color: white;
    box-shadow: 0 0 10px 0 rgb(62, 196, 62);
}


@media only screen and (max-width:1000px) {
    .FooterContainer {
        padding: 1rem;
    }

    .FooterContainer img {
        height: 4rem;
    }

    .FooterContainer a>img {
        height: 2rem;
    }

    .FooterRoute a {
        padding: .5rem 1rem;
        border-radius: 1rem;
        font-size: .7rem;
    }
}