* {
    margin: 0;
    padding: 0;
}

a {
    cursor: pointer;
    text-decoration: none;
}

.TopbarrContainer {
    display: flex;
    flex-direction: row;
    background-color: black;
    justify-content: space-around;
    align-items: center;
    color: white;
    height: 6rem;
}

.TopbarrContainer div {
    width: 30%;
    display: flex;
    justify-content: center;
}

.TopbarrLogo img {
    height: 10rem;
}

.TopbarrOrça a {
    color: white;
    padding: 1rem 2rem;
    border-radius: 2rem;
    background-color: rgb(62, 196, 62);
    box-shadow: 0 0 10px 0 rgb(62, 196, 62);
    transition: .5s ease-in-out;
}

.TopbarrLinks img {
    width: 2rem;
    height: 2rem;
    opacity: .5;
    transition: .5s ease-in-out;
}

.TopbarrLinks img:hover {
    opacity: 1;
}

@media only screen and (max-width:1000px) {
    .TopbarrContainer {
        padding-left: 1rem;
    }

    .TopbarrLogo img {
        height: 6rem;
    }

    .TopbarrOrça {
        font-size: .7rem;
    }

}