.DepositionsTitle {
    text-align: center;
}

.CardElementsContainer {
    display: grid;
    grid-template-columns: 40rem 40rem;
    gap: 2rem;
    grid-template-rows: auto;
    justify-content: center;
    padding: 2rem;
}

.DepositionsCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 15rem;
    border-radius: 10px;
    box-shadow: 0 0 20px 0 rgb(62, 196, 62);
}

.FiveStars {
    display: flex;
    justify-content: center;
    align-items: center;
}

.FiveStars img {
    width: 1.5rem;
    height: 1.5rem;
}

.DepositionsAvaliation p {
    padding: 1rem;
}

.DepositionsPerson h2 {
    font-weight: bolder;
}

@media only screen and (max-width:1000px) {
    .CardElementsContainer {
        grid-template-columns: 20rem;
        gap: 2rem;
        grid-template-rows: auto;
        justify-content: center;
        padding: 2rem;
    }
}